html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  font-size: 1em; }

body {
  font-family: "Crimson Text", serif; }

.content-width {
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 0.8em; }

.overlay-popup {
  display: none; }
  .overlay-popup .background-overlay {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(55, 55, 55, 0.42); }
  .overlay-popup form {
    position: fixed;
    top: 78px;
    left: 50%;
    transform: translateX(-50%);
    width: 320px;
    background-color: white;
    margin-left: auto;
    margin-right: auto; }
    .overlay-popup form h1 {
      text-align: center;
      margin-top: 15px;
      margin-bottom: 20px; }
    .overlay-popup form input {
      display: block;
      width: calc(80% - 10px);
      padding-left: 10px;
      height: 30px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px; }
    .overlay-popup form button {
      display: block;
      width: 100px;
      height: 40px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
      background-color: white;
      border: solid 2px black; }
      .overlay-popup form button:hover {
        cursor: pointer;
        background-color: black;
        color: white;
        transition: all .5s ease-in-out; }

section.login {
  font-family: 'Crimson Text', serif; }
  section.login .container {
    width: 320px;
    height: 285px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto; }
    section.login .container h2 {
      font-size: 35px;
      margin-bottom: 10px; }
    section.login .container form {
      background-color: #373737;
      box-shadow: 10px 10px 29px -13px rgba(0, 0, 0, 0.75); }
      section.login .container form.invalid {
        animation: shake .5s linear; }
    section.login .container input {
      background-color: #373737;
      font-weight: 600;
      color: white;
      width: calc(100% - 50px);
      height: 35px;
      margin: 10px 20px;
      padding-left: 10px;
      border: none;
      border-bottom: solid 2px #D0D0D0;
      outline: none; }
      section.login .container input:focus {
        border-color: white;
        transition: border-color .5s ease-in; }
      section.login .container input:first-child {
        margin-top: 35px; }
    section.login .container button {
      background-color: #373737;
      font-weight: 600;
      color: white;
      width: calc(100% - 40px);
      height: 45px;
      margin: 40px 20px;
      border: solid 2px #D0D0D0; }
      section.login .container button:hover {
        border-color: white;
        background-color: white;
        color: #373737;
        transition: all .5s ease-in-out;
        cursor: pointer; }

@-webkit-keyframes shake {
  8%, 41% {
    transform: translateX(-10px); }
  25%, 58% {
    transform: translateX(10px); }
  75% {
    transform: translateX(-5px); }
  92% {
    transform: translateX(5px); }
  0%, 100% {
    transform: translateX(0); } }

.top-section {
  position: relative;
  margin-top: 20px;
  border-bottom: solid 2px black; }
  @media only screen and (max-width: 1023px) {
    .top-section {
      margin-left: 10px;
      margin-right: 10px; } }
  .top-section button {
    position: absolute;
    right: 0;
    top: 0;
    background-color: white;
    border: solid 2px black;
    height: 41px;
    width: 100px;
    text-align: center;
    line-height: 37px; }
    .top-section button:hover {
      cursor: pointer;
      background-color: black;
      color: white;
      transition: all .5s ease-in-out; }

table.events {
  width: 100%;
  min-width: 320px;
  margin-top: 15px;
  margin-bottom: 15px; }
  @media only screen and (max-width: 1023px) {
    table.events {
      width: calc(100% - 20px);
      margin-left: 10px;
      margin-right: 10px; } }
  table.events thead {
    font-weight: bold; }
    table.events thead th {
      display: none;
      padding-bottom: 10px; }
      @media only screen and (min-width: 480px) {
        table.events thead th {
          display: table-cell; } }
  table.events tbody {
    text-align: center; }
    table.events tbody tr {
      border-top: solid 1px #e4e4e4;
      border-bottom: solid 1px #e4e4e4; }
    table.events tbody td {
      display: block;
      padding: 5px 0; }
      @media only screen and (min-width: 480px) {
        table.events tbody td {
          display: table-cell; } }
      table.events tbody td a {
        text-decoration: none;
        color: black; }
        table.events tbody td a:hover {
          cursor: pointer;
          text-decoration: underline;
          transition: font-weight .5s ease-in-out; }
      table.events tbody td:before {
        content: attr(data-th) ": ";
        font-weight: bold;
        display: inline-block; }
        @media only screen and (min-width: 480px) {
          table.events tbody td:before {
            display: none; } }
      table.events tbody td:last-child:before {
        content: none; }

footer {
  margin-top: 20px;
  text-align: center; }
  footer i {
    font-style: italic; }
  footer a {
    display: block;
    margin-top: 5px;
    text-transform: uppercase;
    text-decoration: none;
    color: black; }
    footer a:hover {
      font-weight: bold;
      transition: font-weight .5s ease-in-out; }
